import * as React from "react";
import HomeLayout from "../layout/HomeLayout";
import IMG1 from "../images/home_1.png";
const IndexPage = () => {
  return (
    <HomeLayout>
      <section className="grid grid-cols-1 lg:grid-cols-2 gap-8 w-full min-h-screen ">
        
        <div className="z-50 ">
          <h1 className=" text-white text-center md:text-left text-5xl md:text-7xl xl:text-9xl capitalize">“LET MY COUNTRY AWAKE”</h1>
          <h2 className="text-white text-center md:text-left text-3xl md:text-5xl xl:text-7xl">THE STUDENT'S FESTIVAL OF LITERATURE - 2023</h2>
        </div>
        <div className="relative h-96 md:h-auto">
          <div className="absolute w-full h-full top-20 -left-12 bg-neutral-900 opacity-50">
          <hr className="absolute bottom-8 -left-96 w-[200%] border border-neutral-500"/>
          <hr className="absolute -top-4 -right-96 w-[150%] border border-neutral-500"/>
          <hr className="absolute -top-4 -right-96 rotate-90 w-[100%] border border-neutral-500"/>
          <hr className="absolute bottom-48 -left-48 rotate-90 w-[75%] border border-neutral-500"/>
          </div>
            <img src={IMG1} alt="Ruusara 2023" className="w-full absolute bottom-0 right-0-0" />
        </div>
      </section>
     
    </HomeLayout>
  );
};

export default IndexPage;

export const Head = () => <title>Home Page</title>;
